<template>
	<div>
		<NavMenu></NavMenu>
		<Breadcrumb id="Breadcrumb"></Breadcrumb>
		<el-divider id="divider1"></el-divider>
			<el-tabs v-model="activeName" type="border-card" @tab-click="handleClick" id="tabs">
			    <el-tab-pane label="" name="first" id="real">
					<router-view></router-view>
				</el-tab-pane>
			    <el-tab-pane label="" name="second" id="history">
					<router-view></router-view>
				</el-tab-pane>
			</el-tabs>
	</div>
</template>

<script>
	import NavMenu from '../../components/NavMenu.vue'
	import Breadcrumb from '../../components/Breadcrumb.vue'
	
	export default{
		components: {
		  NavMenu,
		  Breadcrumb,
		},
		data() {
		  return {
		    activeName: "first"
		  };
		},
		mounted() {
		    //location.searchurl
		    this.updateType();
		},
		methods: {
		  handleClick(tab, event) {
		    console.log(tab, event);
			if(tab.name === "first"){
				this.$router.push('/video/realtime');
			}
			else{
				this.$router.push('/video/historytime');
			}
		  },
		  updateType() {
		        let type = this.$route.path;
			  	console.log(type);
		  		if(type === '/video/realtime'){
					this.activeName = "first";
				}
				else if(type === '/video/historytime'){
					this.activeName = "second";
				}
				else{
					this.$router.push('/video/realtime');
				}
		        //activeName
		        console.log(type);
		  },
		}
	}
</script>

<style>
	#divider1{
		width: 86%;
		margin-top: 23px;
		margin-left: 244px;
	}
	#Breadcrumb{
		margin-top: 20px;
		margin-left: 300px;
	}
	#tabs{
		width: 86%;
		margin-top: -23px;
		margin-left: 244px;
	}
	#history{
		height: 560px;
	}
	#real{
		height: 560px;
	}
</style>
